body {
  color: #757e63;
  background-color: #f5f5f5;
  font-family: helvetica neue;
}

.menu-container {
  flex-direction: column;
  align-items: center;
  margin-left: 5%;
  display: flex;
}

#menu-box {
  text-align: center;
  margin-top: 30px;
  margin-bottom: 15px;
}

.menu-image {
  width: 100px;
  height: 100px;
}

#page-header {
  flex-direction: column;
  align-items: flex-end;
  display: flex;
}

h1 {
  margin-bottom: 0%;
  font-size: 50px;
  font-weight: 200;
}

h2 {
  margin-top: 0%;
  font-weight: 200;
}

ul {
  text-align: left;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

ul li {
  margin-bottom: 15px;
  font-size: 20px;
  font-weight: 200;
}

li:hover {
  color: #1d1b1b;
}

li:first-child {
  margin-bottom: 30px;
}

a {
  color: inherit;
  text-decoration: none;
}

#current {
  text-decoration-line: underline;
  text-decoration-thickness: 1px;
}

#top {
  font-size: 30px;
}

.container {
  grid-template: "left right right right right" 1fr
  / 20% 20% 20% 20% 20%;
  min-height: 98vh;
  display: grid;
}

.main-container {
  grid-area: right;
}

.holding-container {
  width: auto;
}

.list-central-container {
  width: 1000px;
}

.container-list {
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.list {
  flex: 1;
  width: 50%;
  margin: 0;
  padding: 0;
  font-size: 20px;
  font-weight: 200;
  list-style-type: none;
  display: flex;
}

.list li {
  padding: 0 12px;
}

#rowOne li, #rowTwo li {
  margin-bottom: 20px;
}

#rowOne li:hover, #rowTwo li:hover {
  color: #757e63;
}

.container-graph {
  flex: 2;
  justify-content: left;
  width: 1000px;
  height: 600px;
  display: flex;
}

#map {
  width: 100%;
}
